.container-card1{
    display: block;
    box-shadow: black 5px 10px 10px;
}

.bg-image{
    height: 550px;
    width: 1166px;
}

.container-card2{
    position: absolute;
    display: flex;
    flex-direction: column;
    top:0px;
    right: 0px;
    background: rgba(0, 0, 0, 0.7);
    height: 550px;
}

.input-form{
    width: 24em;
    margin: 30px 24px 30px 40px;
    height: 2em;
    padding: 5px;
    color: #718583;
    background: rgba(0, 0, 0, 0);
    border-bottom: solid 2px #9ea7a6;
    border-radius: 25px;
}

.h3-heading{
    color: white;
    margin-left: 40px;
    font-family: 'Merriweather', serif
}

hr{
    width: 70%;
}


li{
    list-style-type: none;
    margin: 10px 0px 10px 0px;
    color: #718583;
    
}
span{
    color: white;
}

.span1{    
    margin-left: 15em;
}

.span2{    
    margin-left: 13.4em;
}
.span3{    
    margin-left: 15.1em;
}
.span4{    
    margin-left: 13.3em;
}
.span5{    
    margin-left: 13.2em;
}

.h2-title{
    position: absolute;
    top:5px;
    left:50px;
    color: white;
    font-family: 'Source Serif 4', sans-serif;
}
.container-card3{
    position: absolute;
    bottom: 20px;
    display: flex;
    flex-direction: row;
    margin-left: 30px;
}
.sup1{
    position: absolute;
    font-size: 40px;
}
.sup2{
    font-size: 10px;
}
.temp-show{
    color: white;
    font-size: 5.4em;
}
.state-show{
    display: flex;
    flex-direction: column;
    color: white;
    margin-left: 24px;
    margin-top: 2.8em;
    font-size: 2em;
    font-family: 'Source Serif 4', sans-serif;
}
.time-span{
    font-size: 20px
}
.main-show{
    display: flex;
    flex-direction: column;
    margin-top: 70px;
    margin-left: 20px;
    font-size: 30px;
    color: white;
}
.icon-image{
    margin: 0;
    padding: 0;
}
.weather-icon-span{
    position: absolute;
    top: 142px;
    right: 20px;
    font-size: 25px;
}
.search-button{
    display: block;
    margin-left: auto;
    margin-right: auto;
    width: fit-content;
    font-size: 15px;
    border-radius: 25px;
    background-color: #4f6764;
    color: white;
    padding: 5px;
    border: 0;
}

.main-container{
    transition: transform 500ms ease-in-out 5ms;
    position: fixed;
    margin-top: 5em;
    margin-left: 10em;

}

.main-container:hover{
   transform: scale(1.03); 
}